.page-footer__copyright {
  display: flex;
  margin: 37px 96px 40px 94px;
}

.page-footer__copyright-text {
  font-size: 14px;
  line-height: 18px;
  font-weight: normal;
  color: #283645;
  letter-spacing: 0.3px;
  padding-right: 8px;
}

.page-footer__copyright-link {
  display: flex;
  cursor: pointer;
}

.page-footer__logo {
  margin-left: 86px;
  margin-right: 87px;
  margin-bottom: 37px;

  &:active {
    opacity: 0.3;
  }
}

.page-footer__social {
  display: flex;
  justify-content: center;
  align-items: center;
  padding-left: 17px;
  padding-right: 0;
  list-style: none;
}

.page-footer__htmlacademy:hover {
  fill: #283645;

  &:active {
    fill: #bec2c7;
  }
}

.page-footer__social-btn {
  width: 46px;
  height: 46px;
  border: solid 3px #e9ebec;
  border-radius: 50%;
  text-align: center;
  margin-right: 17px;
}

.page-footer__social-btn:hover {
  border: solid 3px #283645;
}

.page-footer__social-svg {
  margin-top: 12px;

  &:active {
    fill: #f1becc;
  }
}

.page-footer__logo-desktop {
  display: none;
}

.page-footer__logo-image {
  fill: #283645;

  &:hover {
    fill: #d22856;
  }
}

@media (min-width: @tablet-width) {
  .page-footer {
    margin-top: 7px;
    display: flex;
    width: 100%;
  }

  .page-footer__logo {
    width: 310px;
    margin-left: 20px;
    margin-top: 27px;
    margin-right: 69px;
  }

  .page-footer__copyright {
    width: 310px;
    display: flex;
    align-items: center;
    align-content: center;
    margin-right: 0;
    margin-left: 81px;
    margin-top: 39px;
  }

  .page-footer__social {
    width: 310px;
    padding: 0;
    margin-top: 3px;
  }

  .page-footer__social-btn {
    width: 52px;
    height: 50px;
    margin-right: 10px;
  }

  .page-footer__copyright-text {
    margin-top: 8px;
  }

  .page-footer__social-svg {
    margin-top: 16px;
  }

  .page-footer__facebook {
    margin-top: 12px;
  }
}


@media (min-width: @desktop-width) {
  .page-footer {
    justify-content: space-between;
    width: 100%;
    margin-top: 4px;
  }

  .page-footer__logo {
    padding-left: 11px;
    margin-top: 37px;
    margin-left: 0;
    margin-right: 0;
  }

  .page-footer__social-btn {
    margin-right: 0;
    margin-left: 11px;
  }

  .page-footer__social {
    justify-content: center;
    padding-left: 0px;
    margin-top: 15px;
    margin-right: 122px;
  }

  .page-footer__copyright {
    width: 200px;
    margin-left: 0;
    padding-left: 35px;
  }

  .page-footer__copyright-text {
    font-size: 18px;
    font-weight: normal;
    padding-right: 10px;
  }

  .page-footer__logo-mobile {
    display: none;
  }

  .page-footer__logo-desktop {
    display: block;
  }

  .page-footer__copyright-link {
    margin-top: 7px;
  }
}
